// ========== Helper Functions ==========
function getStatusColor(status) {
  switch (status) {
    case "active": return THEME.primary;
    case "idle": return "#eab308";
    case "busy": return THEME.purple;
    case "offline": return "#6b7280";
    default: return "#6b7280";
  }
}

function formatTime(ts) {
  const diff = Date.now() - ts;
  const mins = Math.floor(diff / 60000);
  if (mins < 1) return "Just now";
  if (mins < 60) return `${mins}m ago`;
  const hrs = Math.floor(mins / 60);
  return `${hrs}h ago`;
}

function formatDate(ts) {
  return new Date(ts).toLocaleString();
}

// ========== Main Component ==========
export default function MissionControl() {
  const [activeTab, setActiveTab] = useState("board");
  const [activityFilter, setActivityFilter] = useState({ agent: "", type: "", hours: "24" });
  const [editingDoc, setEditingDoc] = useState(null);
  const [newDocOpen, setNewDocOpen] = useState(false);

  // Real-time queries
  const dashboard = useQuery(api.getDashboard.default);
  const taskBoard = useQuery(api.getTasksByStatus.default);
  const activities = useQuery(api.getActivitiesFiltered.default, activityFilter.hours !== "24" || activityFilter.agent || activityFilter.type ? {
    limit: 50,
    since: activityFilter.hours !== "24" ? Date.now() - parseInt(activityFilter.hours) * 3600000 : undefined,
    agentId: activityFilter.agent || undefined,
    type: activityFilter.type || undefined,
  } : { limit: 50 });
  const documents = useQuery(api.getDocuments.default);

  // Mutations
  const updateTaskStatus = useMutation(api.updateTaskStatus.default);
  const createDocument = useMutation(api.createDocument.default);
  const updateDocument = useMutation(api.updateDocument.default);
  const deleteDocument = useMutation(api.deleteDocument.default);
  const heartbeat = useMutation(api.heartbeat.default);

  // Heartbeat on mount
  useEffect(() => {
    if (dashboard?.agents?.[0]?._id) {
      heartbeat({ agentId: dashboard.agents[0]._id, status: "active" });
    }
  }, [dashboard, heartbeat]);

  const handleTaskDrop = async (taskId, newStatus) => {
    try {
      await updateTaskStatus({ taskId, status: newStatus, agentId: "cestra" });
    } catch (error) {
      console.error("Drop error:", error);
    }
  };

  // Loading state
  if (dashboard === undefined || taskBoard === undefined) {
    return (
      <div style={styles.loadingContainer}>
        <style>{"@keyframes float { 0%, 100% { transform: translateY(0); } 50% { transform: translateY(-10px); } }"}</style>
        <div style={styles.loadingEmoji}></div>
        <div style={styles.loadingText}>INITIALIZING MISSION CONTROL...</div>
      </div>
    );
  }

  const agents = dashboard?.agents || [];
  const pending = taskBoard?.pending || [];
  const inProgress = taskBoard?.in_progress || [];
  const review = taskBoard?.review || [];
  const completed = taskBoard?.completed || [];

  return (
    <div style={styles.container}>
      {/* Header */}
      <header style={styles.header}>
        <div>
          <h1 style={styles.title}>
            <span style={{
              background: `linear-gradient(135deg, ${THEME.primary}, ${THEME.accent})`,
              WebkitBackgroundClip: "text",
              WebkitTextFillColor: "transparent",
            }}> MISSION CONTROL</span>
          </h1>
          <p style={styles.subtitle}>DC81 AUTONOMOUS AGENT SQUAD</p>
        </div>
        <div style={styles.headerRight}>
          <div style={styles.headerMeta}>
            {agents.length} AGENTS  REAL-TIME UPDATES
          </div>
          <div style={styles.liveBadge}>
            <span style={styles.liveDot}></span>
            <span style={styles.liveText}>LIVE</span>
          </div>
        </div>
      </header>

      {/* Tab Navigation */}
      <div style={styles.tabRow}>
        <button
          onClick={() => setActiveTab("board")}
          style={styles.tabButton(activeTab === "board")}
        >
           KANBAN BOARD ({pending.length + inProgress.length})
        </button>
        <button
          onClick={() => setActiveTab("documents")}
          style={styles.tabButton(activeTab === "documents")}
        >
           DOCUMENTS ({(documents || []).length})
        </button>
      </div>

      {/* Agent Grid */}
      <section style={styles.section}>
        <h2 style={styles.sectionTitle}>
           AGENTS <span style={styles.agentCount}>({agents.length})</span>
        </h2>
        <div style={styles.agentGrid}>
          {agents.map((agent) => (
            <div
              key={agent._id}
              style={styles.agentCard(agent.status)}
            >
              <div style={{ display: "flex", justifyContent: "space-between", alignItems: "start" }}>
                <div>
                  <div style={styles.agentName}>{agent.name}</div>
                  <div style={styles.agentRole}>{agent.role}</div>
                </div>
                <span style={styles.statusBadge(agent.status)}>{agent.status}</span>
              </div>
              {agent.lastHeartbeat && (
                <div style={styles.heartbeat}> {formatTime(agent.lastHeartbeat)}</div>
              )}
            </div>
          ))}
        </div>
      </section>

      {/* Tab Content */}
      {activeTab === "board" ? (
        <KanbanBoard
          tasks={{ pending, in_progress: inProgress, review, completed }}
          onDrop={handleTaskDrop}
        />
      ) : (
        <DocumentsPanel
          documents={documents}
          newDocOpen={newDocOpen}
          setNewDocOpen={setNewDocOpen}
          editingDoc={editingDoc}
          setEditingDoc={setEditingDoc}
          formatDate={formatDate}
          createDoc={createDocument}
          updateDoc={updateDocument}
          deleteDoc={deleteDocument}
        />
      )}

      {/* Activity Feed */}
      <section style={styles.section}>
        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: "15px" }}>
          <h2 style={styles.sectionTitle}> ACTIVITY FEED</h2>
          <div style={styles.filterRow}>
            <select
              value={activityFilter.agent}
              onChange={(e) => setActivityFilter(f => ({ ...f, agent: e.target.value }))}
              style={styles.filterSelect}
            >
              <option value="">ALL AGENTS</option>
              {agents.map((a) => (
                <option key={a._id} value={a._id}>{a.name}</option>
              ))}
            </select>
            <select
              value={activityFilter.type}
              onChange={(e) => setActivityFilter(f => ({ ...f, type: e.target.value }))}
              style={styles.filterSelect}
            >
              <option value="">ALL TYPES</option>
              <option value="heartbeat">HEARTBEAT</option>
              <option value="task_started">TASK STARTED</option>
              <option value="task_completed">TASK COMPLETED</option>
              <option value="message">MESSAGE</option>
            </select>
            <select
              value={activityFilter.hours}
              onChange={(e) => setActivityFilter(f => ({ ...f, hours: e.target.value }))}
              style={styles.filterSelect}
            >
              <option value="1">LAST 1H</option>
              <option value="6">LAST 6H</option>
              <option value="24">LAST 24H</option>
              <option value="168">LAST 7D</option>
            </select>
          </div>
        </div>
        <div style={styles.activityFeed}>
          {activities === undefined ? (
            <p style={{ color: "#64748b", textAlign: "center" }}>LOADING...</p>
          ) : (activities || []).length === 0 ? (
            <p style={{ color: "#64748b", textAlign: "center" }}>NO ACTIVITY MATCHES FILTERS</p>
          ) : (
            <div>
              {(activities || []).map((act) => (
                <div key={act._id} style={styles.activityItem}>
                  <div style={styles.activityAvatar}>
                    {act.agentName?.charAt(0) || "?"}
                  </div>
                  <div style={styles.activityContent}>
                    <div style={styles.activityMessage}>
                      <span style={styles.activityAgent}>{act.agentName}</span>{" "}
                      <span style={styles.activityText}>{act.message}</span>
                    </div>
                    <div style={styles.activityTime}>{formatTime(act.timestamp)}</div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </section>
    </div>
  );
}

// ========== Kanban Board Component ==========
function KanbanBoard({ tasks, onDrop }) {
  const [draggedTask, setDraggedTask] = useState(null);

  const handleDragStart = (e, task) => {
    setDraggedTask(task);
    e.dataTransfer.effectAllowed = "move";
  };

  const handleDragOver = (e) => {
    e.preventDefault();
    e.dataTransfer.dropEffect = "move";
  };

  const handleDrop = (e, newStatus) => {
    e.preventDefault();
    if (draggedTask && draggedTask.status !== newStatus) {
      onDrop(draggedTask._id, newStatus);
    }
    setDraggedTask(null);
  };

  const columns = [
    { key: "pending", label: "TO DO", color: "#eab308" },
    { key: "in_progress", label: "IN PROGRESS", color: THEME.primary },
    { key: "review", label: "REVIEW", color: THEME.purple },
    { key: "completed", label: "DONE", color: "#22c55e" },
  ];

  return (
    <div style={styles.kanbanBoard}>
      {columns.map((col) => {
        const taskList = tasks[col.key] || [];
        return (
          <div
            key={col.key}
            style={styles.kanbanColumn}
            onDragOver={handleDragOver}
            onDrop={(e) => handleDrop(e, col.key)}
          >
            <div style={styles.columnHeader(col.color)}>
              <span>{col.label}</span>
              <span style={styles.taskCount}>{taskList.length}</span>
            </div>
            <div style={{ display: "flex", flexDirection: "column", gap: "8px" }}>
              {taskList.map((task) => (
                <div
                  key={task._id}
                  draggable
                  onDragStart={(e) => handleDragStart(e, task)}
                  style={{
                    ...styles.taskCard,
                    opacity: draggedTask?._id === task._id ? 0.5 : 1,
                  }}
                >
                  <div style={styles.taskTitle}>{task.title}</div>
                  {task.priority && (
                    <span style={styles.priorityBadge(task.priority)}>
                      {task.priority.toUpperCase()}
                    </span>
                  )}
                </div>
              ))}
            </div>
          </div>
        );
      })}
    </div>
  );
}

// ========== Documents Panel Component ==========
function DocumentsPanel({ documents, newDocOpen, setNewDocOpen, editingDoc, setEditingDoc, formatDate, createDoc, updateDoc, deleteDoc }) {
  const [form, setForm] = useState({ title: "", content: "", type: "other" });

  const handleCreate = async () => {
    try {
      await createDoc({ ...form, authorId: "cestra" });
      setForm({ title: "", content: "", type: "other" });
      setNewDocOpen(false);
    } catch (error) {
      console.error("Create error:", error);
    }
  };

  const handleUpdate = async () => {
    if (!editingDoc) return;
    try {
      await updateDoc({
        documentId: editingDoc._id,
        title: editingDoc.title,
        content: editingDoc.content,
        authorId: "cestra",
      });
      setEditingDoc(null);
    } catch (error) {
      console.error("Update error:", error);
    }
  };

  const handleDelete = async (id) => {
    if (!confirm("Delete this document?")) return;
    try {
      await deleteDoc({ documentId: id, authorId: "cestra" });
    } catch (error) {
      console.error("Delete error:", error);
    }
  };

  return (
    <div>
      {newDocOpen && (
        <div style={styles.docPanel}>
          <h3 style={styles.docFormTitle}>NEW DOCUMENT</h3>
          <input
            value={form.title}
            onChange={(e) => setForm(f => ({ ...f, title: e.target.value }))}
            placeholder="TITLE"
            style={styles.docInput}
          />
          <select
            value={form.type}
            onChange={(e) => setForm(f => ({ ...f, type: e.target.value }))}
            style={styles.docSelect}
          >
            <option value="spec">SPEC</option>
            <option value="memo">MEMO</option>
            <option value="decision">DECISION</option>
            <option value="other">OTHER</option>
          </select>
          <textarea
            value={form.content}
            onChange={(e) => setForm(f => ({ ...f, content: e.target.value }))}
            placeholder="CONTENT"
            rows={4}
            style={styles.docTextarea}
          />
          <div>
            <button onClick={handleCreate} style={styles.docButton(true)}>
              CREATE
            </button>
            <button onClick={() => setNewDocOpen(false)} style={styles.docButton(false)}>
              CANCEL
            </button>
          </div>
        </div>
      )}

      {editingDoc && (
        <div style={{ ...styles.docPanel, border: `2px solid ${THEME.primary}` }}>
          <h3 style={styles.docFormTitle}>EDIT DOCUMENT</h3>
          <input
            value={editingDoc.title}
            onChange={(e) => setEditingDoc(d => d ? { ...d, title: e.target.value } : null)}
            style={styles.docInput}
          />
          <textarea
            value={editingDoc.content}
            onChange={(e) => setEditingDoc(d => d ? { ...d, content: e.target.value } : null)}
            rows={4}
            style={styles.docTextarea}
          />
          <div>
            <button onClick={handleUpdate} style={styles.docButton(true)}>
              SAVE
            </button>
            <button onClick={() => setEditingDoc(null)} style={styles.docButton(false)}>
              CANCEL
            </button>
          </div>
        </div>
      )}

      <button onClick={() => setNewDocOpen(true)} style={styles.newDocButton}>
        + NEW DOCUMENT
      </button>

      {documents === undefined ? (
        <p style={{ color: "#64748b", textAlign: "center" }}>LOADING...</p>
      ) : (documents || []).length === 0 ? (
        <p style={{ color: "#64748b", textAlign: "center" }}>NO DOCUMENTS YET</p>
      ) : (
        <div style={{ display: "grid", gridTemplateColumns: "repeat(auto-fill, minmax(300px, 1fr))", gap: "12px" }}>
          {(documents || []).map((doc) => (
            <div key={doc._id} style={{ background: THEME.card, borderRadius: "10px", padding: "15px", border: `1px solid ${THEME.border}` }}>
              <div style={{ display: "flex", justifyContent: "space-between", alignItems: "start", marginBottom: "8px" }}>
                <h4 style={{ margin: 0, fontSize: "14px", fontFamily: "'Orbitron', sans-serif" }}>{doc.title}</h4>
                <span style={{ fontSize: "10px", padding: "2px 6px", borderRadius: "4px", background: THEME.muted, textTransform: "uppercase" }}>{doc.type}</span>
              </div>
              <p style={{ margin: "0 0 10px", fontSize: "12px", color: "#94a3b8", overflow: "hidden", textOverflow: "ellipsis", whiteSpace: "nowrap" }}>{doc.content}</p>
              <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", fontSize: "11px", color: "#64748b" }}>
                <span>{formatDate(doc.updatedAt)}</span>
                <div style={{ display: "flex", gap: "8px" }}>
                  <button onClick={() => setEditingDoc(doc)} style={{ padding: "4px 8px", borderRadius: "4px", border: "none", background: THEME.muted, color: THEME.foreground, cursor: "pointer", fontSize: "10px" }}>EDIT</button>
                  <button onClick={() => handleDelete(doc._id)} style={{ padding: "4px 8px", borderRadius: "4px", border: "none", background: THEME.destructive, color: THEME.foreground, cursor: "pointer", fontSize: "10px" }}>DELETE</button>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
