// App.jsx - Convex Provider Wrapper (No Clerk for now)
// Direct Convex connection for dashboard access

import { ConvexProvider } from "convex/react";
import { ConvexReactClient } from "convex/react";
import MissionControl from "./MissionControl";
import { useEffect, useState } from "react";

// Convex client configuration - Direct connection
const convexUrl = "https://exciting-warbler-274.eu-west-1.convex.cloud";

const convex = new ConvexReactClient(convexUrl);

function AppContent() {
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) {
    return (
      <div style={{ 
        fontFamily: "system-ui, sans-serif", 
        maxWidth: "1400px", margin: "0 auto", padding: "20px", 
        backgroundColor: "#0f172a", minHeight: "100vh", color: "#e2e8f0",
        display: "flex", alignItems: "center", justifyContent: "center"
      }}>
        <div style={{ textAlign: "center" }}>
          <div style={{ fontSize: "48px", marginBottom: "20px" }}></div>
          <div style={{ fontSize: "18px" }}>Initializing Mission Control...</div>
        </div>
      </div>
    );
  }

  return (
    <ConvexProvider client={convex}>
      <MissionControl />
    </ConvexProvider>
  );
}

export default AppContent;
